/* Details Container */
.details-container {
    min-height: 100vh;
    background: var(--light-green);
}

/* Loading State */
.loading-state {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 50vh;
    text-align: center;
}

.loading-state .spinner {
    width: 50px;
    height: 50px;
    border: 4px solid var(--light-green);
    border-top: 4px solid var(--primary-color);
    border-radius: 50%;
    animation: spin 1s linear infinite;
    margin-bottom: 1.5rem;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

/* Error State */
.error-state {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 50vh;
    text-align: center;
    padding: 2rem;
}

.error-state i {
    font-size: 4rem;
    color: var(--danger-color);
    margin-bottom: 1.5rem;
}

.error-state h3 {
    font-size: 1.8rem;
    margin-bottom: 1rem;
    color: var(--dark-green);
}

.error-state p {
    margin-bottom: 2rem;
    color: var(--text-color);
}

.error-state .btn {
    margin: 0.5rem;
}

/* Details Header */
.details-header {
    background: var(--white-color);
    padding: 1rem 0;
    border-bottom: 1px solid var(--border-color);
}

.back-btn {
    background: transparent;
    border: none;
    color: var(--primary-color);
    font-size: 1rem;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.5rem 1rem;
    border-radius: 8px;
    transition: all 0.3s ease;
}

.back-btn:hover {
    background: var(--light-green);
}

/* Image Gallery */
.image-gallery {
    background: var(--white-color);
    padding: 2rem 0;
}

.main-image {
    position: relative;
    width: 100%;
    max-height: 500px;
    overflow: hidden;
    border-radius: 16px;
    margin-bottom: 1rem;
}

.main-image img {
    width: 100%;
    height: 500px;
    object-fit: cover;
}

.gallery-nav {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: rgba(255, 255, 255, 0.9);
    border: none;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.3s ease;
    backdrop-filter: blur(10px);
}

.gallery-nav:hover {
    background: var(--white-color);
    transform: translateY(-50%) scale(1.1);
}

.gallery-nav.prev {
    left: 1rem;
}

.gallery-nav.next {
    right: 1rem;
}

.thumbnail-gallery {
    display: flex;
    gap: 0.5rem;
    overflow-x: auto;
    padding: 0.5rem 0;
}

.thumbnail {
    flex-shrink: 0;
    width: 80px;
    height: 60px;
    border-radius: 8px;
    overflow: hidden;
    cursor: pointer;
    border: 2px solid transparent;
    transition: all 0.3s ease;
}

.thumbnail.active {
    border-color: var(--primary-color);
}

.thumbnail img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

/* Accommodation Info */
.accommodation-info {
    padding: 3rem 0;
}

.info-grid {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 3rem;
}

/* Title Section */
.title-section h1 {
    font-size: 2.5rem;
    font-weight: 700;
    color: var(--dark-green);
    margin-bottom: 1rem;
    line-height: 1.2;
}

.location-info {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    color: var(--text-color);
    margin-bottom: 1rem;
    font-size: 1.1rem;
}

.location-info i {
    color: var(--primary-color);
}

.rating-info {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-bottom: 2rem;
}

.stars {
    display: flex;
    gap: 0.25rem;
}

.stars i {
    color: #FFD700;
    font-size: 1.1rem;
}

.rating-info span {
    color: var(--text-color);
    font-weight: 500;
}

/* Description Section */
.description-section {
    margin-bottom: 2rem;
}

.description-section h3 {
    font-size: 1.5rem;
    font-weight: 600;
    color: var(--dark-green);
    margin-bottom: 1rem;
}

.description-section p {
    line-height: 1.6;
    color: var(--text-color);
    font-size: 1.05rem;
}

/* Services Section */
.services-section {
    margin-bottom: 2rem;
}

.services-section h3 {
    font-size: 1.5rem;
    font-weight: 600;
    color: var(--dark-green);
    margin-bottom: 1rem;
}

.services-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 0.75rem;
}

.service-item {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    color: var(--text-color);
}

.service-item i {
    color: var(--primary-color);
    font-size: 0.9rem;
}

/* Capacity Section */
.capacity-section {
    margin-bottom: 2rem;
}

.capacity-section h3 {
    font-size: 1.5rem;
    font-weight: 600;
    color: var(--dark-green);
    margin-bottom: 1rem;
}

.capacity-info {
    display: flex;
    gap: 2rem;
}

.capacity-item {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    color: var(--text-color);
}

.capacity-item i {
    color: var(--primary-color);
    font-size: 1.1rem;
}

/* Booking Card */
.booking-card {
    background: var(--white-color);
    border-radius: 16px;
    padding: 2rem;
    box-shadow: var(--card-shadow);
    position: sticky;
    top: 2rem;
}

.price-info {
    text-align: center;
    margin-bottom: 2rem;
}

.price-label {
    display: block;
    color: var(--text-color);
    font-size: 0.9rem;
    margin-bottom: 0.5rem;
}

.price {
    display: block;
    font-size: 2rem;
    font-weight: 700;
    color: var(--primary-color);
}

.booking-form {
    margin-bottom: 1.5rem;
}

.form-group {
    margin-bottom: 1rem;
}

.form-group label {
    display: block;
    font-size: 0.9rem;
    font-weight: 500;
    color: var(--text-color);
    margin-bottom: 0.5rem;
}

.form-group input,
.form-group textarea {
    width: 100%;
    padding: 0.75rem;
    border: 2px solid var(--border-color);
    border-radius: 8px;
    font-size: 1rem;
    transition: all 0.3s ease;
}

.form-group input:focus,
.form-group textarea:focus {
    outline: none;
    border-color: var(--primary-color);
    box-shadow: 0 0 0 3px rgba(46, 139, 87, 0.1);
}

.booking-actions {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
}

.booking-actions .btn {
    text-align: center;
}

/* Reviews Section */
.reviews-section {
    background: var(--white-color);
    padding: 3rem 0;
    margin-top: 2rem;
}

.reviews-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2rem;
}

.reviews-header h2 {
    font-size: 2rem;
    font-weight: 600;
    color: var(--dark-green);
}

/* Review Form */
.review-form {
    background: var(--light-green);
    border-radius: 12px;
    padding: 2rem;
    margin-bottom: 2rem;
}

.review-form h3 {
    font-size: 1.3rem;
    font-weight: 600;
    color: var(--dark-green);
    margin-bottom: 1.5rem;
}

.rating-input {
    display: flex;
    gap: 0.5rem;
    margin-bottom: 1rem;
}

.rating-input i {
    font-size: 1.5rem;
    color: #ddd;
    cursor: pointer;
    transition: color 0.3s ease;
}

.rating-input i.fa-solid {
    color: #FFD700;
}

.form-actions {
    display: flex;
    gap: 1rem;
    margin-top: 1.5rem;
}

/* Reviews List */
.loading-reviews {
    text-align: center;
    padding: 2rem;
}

.no-reviews {
    text-align: center;
    padding: 2rem;
    color: var(--text-color);
}

.no-reviews i {
    font-size: 3rem;
    color: var(--light-green);
    margin-bottom: 1rem;
}

.review-item {
    border-bottom: 1px solid var(--border-color);
    padding: 2rem 0;
}

.review-item:last-child {
    border-bottom: none;
}

.review-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 1rem;
}

.reviewer-info strong {
    color: var(--dark-green);
    font-size: 1.1rem;
}

.review-date {
    display: block;
    color: var(--text-color);
    font-size: 0.9rem;
    margin-top: 0.25rem;
}

.review-rating {
    display: flex;
    gap: 0.25rem;
}

.review-rating i {
    color: #FFD700;
    font-size: 0.9rem;
}

.review-content p {
    color: var(--text-color);
    line-height: 1.6;
    margin-bottom: 1rem;
}

.review-response {
    background: var(--light-green);
    border-radius: 8px;
    padding: 1rem;
    margin-top: 1rem;
}

.response-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0.5rem;
}

.response-header strong {
    color: var(--primary-color);
    font-size: 0.9rem;
}

.response-date {
    color: var(--text-color);
    font-size: 0.8rem;
}

.review-response p {
    color: var(--text-color);
    font-size: 0.9rem;
    line-height: 1.5;
}

/* Buttons */
.btn {
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 8px;
    font-size: 1rem;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.3s ease;
    text-decoration: none;
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
}

.btn-primary {
    background: var(--primary-color);
    color: var(--white-color);
}

.btn-primary:hover {
    background: var(--accent-color);
    transform: translateY(-2px);
}

.btn-secondary {
    background: var(--text-color);
    color: var(--white-color);
}

.btn-secondary:hover {
    background: var(--dark-green);
}

.btn-outline {
    background: transparent;
    color: var(--primary-color);
    border: 2px solid var(--primary-color);
}

.btn-outline:hover {
    background: var(--primary-color);
    color: var(--white-color);
}

.btn-full {
    width: 100%;
    justify-content: center;
}

.btn:disabled {
    opacity: 0.6;
    cursor: not-allowed;
    transform: none;
}

/* Responsive Design */
@media (max-width: 1024px) {
    .info-grid {
        grid-template-columns: 1fr;
        gap: 2rem;
    }
    
    .booking-card {
        position: static;
    }
}

@media (max-width: 768px) {
    .title-section h1 {
        font-size: 2rem;
    }
    
    .capacity-info {
        flex-direction: column;
        gap: 1rem;
    }
    
    .services-grid {
        grid-template-columns: 1fr;
    }
    
    .reviews-header {
        flex-direction: column;
        gap: 1rem;
        align-items: flex-start;
    }
    
    .form-actions {
        flex-direction: column;
    }
    
    .review-header {
        flex-direction: column;
        gap: 0.5rem;
        align-items: flex-start;
    }
}

@media (max-width: 480px) {
    .title-section h1 {
        font-size: 1.75rem;
    }
    
    .main-image img {
        height: 300px;
    }
    
    .gallery-nav {
        width: 40px;
        height: 40px;
    }
    
    .thumbnail {
        width: 60px;
        height: 45px;
    }
    
    .booking-card {
        padding: 1.5rem;
    }
    
    .price {
        font-size: 1.5rem;
    }
}

/* Focus States for Accessibility */
.btn:focus,
input:focus,
textarea:focus {
    outline: 2px solid var(--primary-color);
    outline-offset: 2px;
}

/* High Contrast Mode Support */
@media (prefers-contrast: high) {
    .booking-card {
        border: 2px solid var(--text-color);
    }
    
    .review-item {
        border-bottom-width: 2px;
    }
}

/* Reduced Motion Support */
@media (prefers-reduced-motion: reduce) {
    .btn:hover,
    .gallery-nav:hover,
    .thumbnail:hover {
        transform: none;
    }
    
    .main-image img,
    .thumbnail img {
        transition: none;
    }
}
